@mixin onPhone {
  @media (max-width: 743px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 744px) and (max-width: 1023px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin top-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
